@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
    font-family: 'Roboto', sans-serif;
}

.wrapper{
    position: relative;
    top: 200vh;
    max-width: 100%;
    background-color:#496480;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    grid-template-rows: 1fr 5fr 1fr;
    gap: 1px 1px;
    grid-template-areas: ". . ." ". . ." ". . .";
}