html {
  scroll-behavior: smooth;
}



.App {
  width: 100%;
  height: 100%;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 1px 1px;
  grid-template-areas: ". g ." ". b ." ". . .";
  background-color:#E1E7E4;
  z-index: 1;
}

.AppMobile{
  background-color: #fdfffa;
}